.auth-wrapper * {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Poppins", sans-serif;
}



.password {
  position: relative;
}

.auth-wrapper {
  display: flex;
  align-items: center;
  justify-content: center;
  background: #f0faff;
  position: relative;
  width: 500px;
  border-radius: 12px;
  padding: 20px 30px 120px;
  background:rgba(28, 24, 38, 1);
  box-shadow: 0 5px 10px rgba(0, 0, 0, 0.1);
  overflow: hidden;
  transition: all 0.3s ease;
}
.OTP{
  display: flex;

}
.OTP-btn{
  width: 30%;
  border-radius: 10px;
 font-size: 18px;
  font-weight: 500;
  cursor: pointer;
  background-color: #fafafa;
  outline: none;
  border: none;
}

.auth-form {
  display: flex;
  flex-direction: column;
  width: 80%;
  align-items: center;
}

.auth-form header {
  font-size: 30px;
  text-align: center;
  color: #fff;
  font-weight: 600;
  cursor: pointer;
}

.auth-wrapper.auth-active .auth-signup header {
  opacity: 0.6;
}

.auth-wrapper.auth-active .auth-login header {
  opacity: 1;
}

.auth-signup header {
  color: #fff;
}

.auth-login header {
  color: #333;
  opacity: 0.6;
}

.auth-wrapper.auth-active .auth-form.auth-login {
  bottom: -10%;
  border-radius: 35%;
  box-shadow: 0 -5px 10px rgba(0, 0, 0, 0.1);
  display: flex;
    flex-direction: column;
    align-items: center
}

.auth-form.auth-login {
  position: absolute;
  left: 50%;
  bottom: -90%;
  transform: translateX(-50%);
  width: calc(100% + 220px);
  padding: 20px 140px;
  border-radius: 50%;
  height: 100%;
  background: #fff;
  transition: all 0.6s ease;
}
.forgot{
  text-align: right;
}
.auth-wrapper form {
  display: flex;
  flex-direction: column;
  grid-gap: 20px;
  gap: 20px;
  margin-top: 40px;
  width: 80%;
}

.auth-wrapper input {
  height: 60px;
  outline: none;
  border: none;
  padding: 0 15px;
  font-size: 16px;
  font-weight: 400;
  color: #333;
  border-radius: 8px;
  background: #fff;
  width: 100%;
  border: 1px solid rgb(44, 44, 44);
}

.auth-checkbox {
  display: flex;
  align-items: center;
  grid-gap: 10px;
  gap: 10px;
}

.auth-checkbox input[type="checkbox"] {
  height: 16px;
  width: 16px;
  accent-color: #fff;
  cursor: pointer;
}

.auth-checkbox label {
  cursor: pointer;
  color: #fff;
}

.auth-wrapper form a {
  color: #333;
  text-decoration: none;
}

.auth-wrapper form a:hover {
  text-decoration: underline;
}

.auth-wrapper input[type="submit"] {
  margin-top: 15px;
  font-size: 18px;
  font-weight: 500;
  cursor: pointer;
}

.auth-form.auth-login input[type="submit"] {
  background: rgba(28, 24, 38, 1);
  color: #fff;
  border: none;
}

